function checkFilterType(e,r){if("string"!=typeof r)throw SyntaxError(e+" param should be a String, not "+typeof r);if(!r.match("AUDIO|AUTODETECT|VIDEO"))throw SyntaxError(e+" param is not one of [AUDIO|AUTODETECT|VIDEO] ("+r+")")}var checkType=require("kurento-client").checkType;module.exports=checkFilterType;